<script>
  import { onMount, onDestroy } from 'svelte'

  import { transitionTime } from '../helpers/transitionTime'
  import { downloadFile } from '../helpers/downloadFile'
  import { cards } from '../cards'

  import { params, navigate } from '../components/Router.svelte'
  import View from '../components/View.svelte'
  import Header from '../components/Header.svelte'
  import Button from '../components/Button.svelte'
  import Card from '../components/Card.svelte'

  // Card Index
  $: i = cards.getIndex(Number($params.get('id')))

  let isMarkdownView = true

  const autoFocus = (node, timeout) => setTimeout(() => node.focus(), timeout)

  const downloadCard = () =>
    downloadFile('card' + $cards[i].id + '.txt', $cards[i].content)

  // Ctrl + S shortcut
  const saveShortcut = (e) => {
    if (
      (window.navigator.platform.match('Mac') ? e.metaKey : e.ctrlKey) &&
      e.keyCode == 83
    ) {
      e.preventDefault()
      downloadCard()
    }
  }

  onMount(() => {
    // Create new card
    if (Boolean($params.get('new'))) {
      cards.create('md')
      navigate('/card?id=' + $cards[0].id, true)
      isMarkdownView = false
    }

    // Add Ctrl + S shortcut
    document.addEventListener('keydown', saveShortcut)
  })

  onDestroy(() => {
    // Remove Ctrl + S shortcut
    document.removeEventListener('keydown', saveShortcut)
  })
</script>

<style>
  .header-content {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  .editor {
    display: block;
    box-sizing: border-box;
    height: calc(100% - 63px);
    padding-top: 1.5rem;
    padding-bottom: 1.5rem;

    color: inherit;
    background: var(--background);
    border: none;

    font-size: 1rem;
    font-family: inherit;
    line-height: var(--line-height);
    letter-spacing: inherit;

    overflow-y: scroll;
    overflow-y: overlay;
    resize: none;
    z-index: 1;
  }

  .markdown-view {
    padding-top: 1.5rem;
    padding-bottom: 1.5rem;
  }
</style>

<View class={isMarkdownView ? 'scrollable' : ''}>
  <Header>
    <div class="header-content">
      <Button
        icon="navigate_before"
        title="Back"
        class="transparent without-left-spacing"
        on:click={() => window.history.back()} />
      <div class="icons">
        <Button
          icon="download"
          title="Download"
          class="transparent"
          on:click={downloadCard} />
        <Button
          icon={isMarkdownView ? 'edit' : 'notes'}
          title={isMarkdownView ? 'Edit' : 'Markdown view'}
          class="transparent"
          on:click={() => (isMarkdownView = !isMarkdownView)} />
        <Button
          icon="delete"
          title="Delete"
          class="transparent without-right-spacing"
          on:click={() => {
            cards.delete($cards[i].id)
            i = -1
            window.history.back()
          }} />
      </div>
    </div>
  </Header>

  {#if Boolean($cards[i])}
    {#if isMarkdownView}
      <div class="markdown-view container selectable">
        <Card card={$cards[i]} />
      </div>
    {:else}
      <textarea
        name="Editor"
        class="editor without-spacing container"
        placeholder="Type here..."
        bind:value={$cards[i].content}
        use:autoFocus={transitionTime} />
    {/if}
  {/if}
</View>
