<script>
  import { slide } from 'svelte/transition'
  import { expoOut } from 'svelte/easing'
  import { transitionTime } from '../helpers/transitionTime'
  import { navigate } from '../components/Router.svelte'
  import Icon from '../components/Icon.svelte'
  import Card from '../components/Card.svelte'

  export let cards
</script>

<style>
  button.card-button {
    display: block;
    width: 100%;
    padding: 0 1rem;
    margin: 0;

    color: inherit;
    background: var(--background);
    border: none;
    border-bottom: 1px solid var(--border-color);

    font-family: inherit;
    text-align: left;
    line-height: inherit;
    letter-spacing: inherit;
    word-spacing: inherit;

    cursor: pointer;
    overflow: hidden;

    transition: padding var(--transition-time);
  }

  button.card-button > :global(.card) {
    max-height: 150px;
    padding: 1.5rem 0;

    overflow: hidden;
    z-index: 1;
    transition: opacity var(--transition-time), transform var(--transition-time);
  }

  button.card-button:active > :global(.card),
  button.card-button:focus > :global(.card) {
    opacity: 0.75;
    transform: scale(0.985);
  }

  .blur {
    --height: 1.5rem;

    position: relative;
    top: calc(150px + var(--height));
    height: var(--height);
    margin-bottom: calc(-1 * var(--height));

    background: linear-gradient(
      0deg,
      var(--background) 0%,
      var(--background) 25%,
      transparent 100%
    );

    z-index: 2;
  }

  .empty {
    display: flex;
    justify-content: center;
    align-items: center;
    height: calc(100vh - (63px * 2));

    color: var(--color-secondary);
  }
</style>

<main class="card-list container extended">
  {#each cards as card, i (card.id)}
    <button
      class="card-button"
      title="Open card"
      transition:slide|local={{ duration: transitionTime, delay: transitionTime / 2 }}
      on:click={() => navigate('/card?id=' + card.id)}>
      <div class="blur" />
      <Card {card} />
    </button>
  {:else}
    <div class="empty">
      <p>
        Tap
        <Icon>add</Icon>
        to add new card
      </p>
    </div>
  {/each}
</main>
