<script>
  import { slide, fly, fade } from 'svelte/transition'
  import { expoOut, cubicOut } from 'svelte/easing'

  import { transitionTime } from '../helpers/transitionTime'

  import { cards } from '../cards'

  import { navigate, hashname } from '../components/Router.svelte'
  import Button from '../components/Button.svelte'
  import View from '../components/View.svelte'
  import Header from '../components/Header.svelte'
  import CardList from '../components/CardList.svelte'

  // Deleting empty cards
  $cards = $cards.filter((card) => card.content !== '')

  // Sort cards by changeTime
  $: sortedCards = $cards.sort((a, b) => b.modifyDate - a.modifyDate)

  // Menu
  $: isMenuOpen = $hashname === 'menu'

  const menuData = [
    {
      icon: 'upload',
      text: 'Upload',
      title: 'Upload file',
      clickHandler() {
        navigate('/')
      },
    },
    {
      icon: 'access_time',
      text: 'Archive',
      title: 'Archive',
      clickHandler() {
        navigate('/archive')
      },
    },
    {
      icon: 'settings',
      text: 'Settings',
      title: 'Settings',
      clickHandler() {
        navigate('/settings')
      },
    },
  ]
</script>

<style>
  .header-content {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  menu {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    margin: 0;
    margin-bottom: 0.25rem;
  }

  .menu-blur {
    position: fixed;
    top: 0;
    width: 100%;
    height: 100vh;
    z-index: 99;
    background: var(--background);
    opacity: 0.75;
  }
</style>

<View class={isMenuOpen ? '' : 'scrollable'}>
  <Header>
    <div class="header-content">
      <h5 class="logo">Your notes</h5>
      <div class="icons">
        {#if !isMenuOpen}
          <Button
            icon="add"
            class="transparent"
            title="New card"
            on:click={() => navigate('/card?new=true')} />
        {/if}
        <Button
          icon={isMenuOpen ? 'expand_less' : 'expand_more'}
          title={isMenuOpen ? 'Close menu' : 'Open menu'}
          class="transparent without-right-spacing"
          on:click={() => (isMenuOpen ? navigate('/', true) : navigate('/#menu'))} />
      </div>
    </div>

    {#if isMenuOpen}
      <menu
        in:slide|local={{ duration: transitionTime, easing: expoOut }}
        out:slide|local={{ duration: transitionTime / 2, easing: expoOut }}>
        {#each menuData as item}
          <Button
            icon={item.icon}
            title={item.title}
            class="without-right-spacing without-top-spacing reversed
            transparent"
            on:click={item.clickHandler}>
            {item.text}
          </Button>
        {/each}
      </menu>
    {/if}
  </Header>

  {#if isMenuOpen}
    <div
      class="menu-blur"
      in:fade|local={{ duration: transitionTime / 2 }}
      out:fade|local={{ duration: transitionTime / 4 }}
      on:click={() => navigate('/')} />
  {/if}

  <CardList cards={sortedCards} />
</View>
