<script>
  import Icon from './Icon.svelte'

  export let icon = null
  export let title = null

  let isIconExists = Boolean(icon)
  let isTextExists = Boolean($$props.$$slots)
</script>

<style>
  button {
    display: inline-flex;
    justify-content: center;
    align-items: center;
    box-sizing: border-box;
    vertical-align: middle;
    padding: var(--padding);

    border: 1px solid transparent;
    border-radius: 50rem;
    outline: none;

    font-size: 1rem;
    font-family: inherit;
    line-height: 1;
    letter-spacing: inherit;
    word-spacing: inherit;

    cursor: pointer;
    transition: opacity var(--transition-time), transform var(--transition-time),
      background var(--transition-time), border var(--transition-time);
  }

  button:active {
    opacity: 0.75;
    transform: scale(0.95);
  }

  /* Text */
  button.text span {
    display: inline-flex;
    vertical-align: top;
    font-weight: bold;
    line-height: 1.5;
  }

  /* Text space */
  button.icon.text span {
    margin: 0 0.25rem;
  }

  /* Button padding size */
  button {
    --padding: 0.5rem;
  }
  button.small {
    --padding: 0.35rem;
  }
  button.big {
    --padding: 0.75rem;
  }

  /* Without spacing */
  button.without-left-spacing {
    margin-left: calc(0rem - var(--padding));
  }
  button.without-right-spacing {
    margin-right: calc(0rem - var(--padding));
  }

  /* Reversed */
  button.reversed {
    flex-direction: row-reverse;
  }

  /* Default */
  button {
    color: var(--color);
    background: var(--surface);
    border-color: var(--surface);
  }
  button:hover,
  button:focus {
    color: var(--color);
    background: var(--surface);
    border-color: var(--border-color);
  }

  /* Outlined */
  button.outlined {
    color: var(--color);
    background: transparent;
    border-color: var(--border-color);
  }
  button.outlined:hover,
  button.outlined:focus {
    color: var(--color);
    background: var(--surface);
    border-color: var(--border-color);
  }

  /* Transparent */
  button.transparent {
    color: var(--color);
    background: transparent;
    border-color: transparent;
  }
  button.transparent:hover,
  button.transparent:focus {
    color: var(--color);
    background: var(--surface);
    border-color: transparent;
  }
</style>

<button
  class={$$props.class || ''}
  class:icon={isIconExists}
  class:text={isTextExists}
  style={$$props.style || ''}
  {title}
  on:click>
  {#if isIconExists}
    <Icon>{icon}</Icon>
  {/if}
  {#if isTextExists}
    <span>
      <slot />
    </span>
  {/if}
</button>
