<script>
  import { scale, fade } from 'svelte/transition'
  import { expoOut } from 'svelte/easing'
  import { transitionTime } from '../helpers/transitionTime'

  export let zIndex = 0
</script>

<style>
  .view {
    position: absolute;
    width: 100%;
    height: 100vh;
    height: -webkit-fill-available;

    background: var(--background);

    overflow: hidden;
  }

  .view.scrollable {
    overflow-y: scroll;
    overflow-y: overlay;
    overflow-x: hidden;
  }

  .view.transparent {
    background: var(--transparent);
  }
</style>

<div
  class="view {$$props.class ? $$props.class : ''}"
  style="z-index: {zIndex}"
  in:scale={{ duration: transitionTime, start: 1.1, easing: expoOut }}
  out:fade={{ duration: transitionTime / 2, easing: expoOut }}>
  <slot />
</div>
